body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html {
  scroll-behavior: smooth; }

span {
  margin-left: 10px; }

.App {
  text-align: center;
  font-family: "Poppins", "Courier New", Courier, monospace;
  background-image: url(/static/media/background.c2a3c37a.jpg);
  background-size: cover;
  height: 100vh;
  background-repeat: no-repeat;
  background-position-y: -50px;
  background-attachment: fixed; }

.link {
  border: 2px solid black;
  color: white;
  padding: 10px;
  text-align: center;
  display: inline-block;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  margin: 5px 0 5px 0; }
  .link:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1); }

.title {
  font-size: 100px;
  position: relative;
  top: 20px;
  right: 110px; }

.vertical {
  width: 70px;
  position: absolute;
  right: 15vw;
  top: 25vh; }

.navigation {
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 50px;
  background-color: black;
  opacity: 90%; }
  .navigation a {
    color: silver;
    font-size: 25px; }

@media only screen and (max-width: 500px) {
  .title {
    font-size: 40px;
    top: 50px;
    right: 10px; }
  .vertical {
    width: 100vw;
    top: 80vh;
    right: 0%;
    display: inline-block; }
  .link {
    margin: 0 5px 0 5px; } }

